import React from 'react';
import './Footer.css';

const Footer = () => {
    return (
        <footer className="disclaimer-premium">
            <div className="text">
                <p className="disclaimer-premium__title">Testar o Premium de graa</p>
                <p className="disclaimer-premium__subtitle">
                    Inscreva-se para curtir msica ilimitada e podcasts s com alguns
                    anncios. No precisa de carto de crdito.
                </p>
            </div>
            <div className="button">
                <button type="button">Inscreva-se grtis</button>
            </div>
        </footer>
    )
};

export default Footer;


